import { httpService } from './http-service'
export const chatService = {
  query,
  saveMessage,
  getRooms,
  createRoom,
  deleteMessage
}
async function query(filterBy = {}) {
  try {
    return await httpService.get('messages/sync', { params: filterBy })
  } catch (err) {
    console.log('Cannot get messages', err)
  }
}

async function saveMessage(message) {
  try {
    return await httpService.post('messages/new', message)
  } catch (err) {
    console.log('Cannot save message', err)
  }
}

async function deleteMessage(messageId) {
  try {
    return await httpService.delete('messages/delete', {_id: messageId})
  } catch (err) {
    console.log('Cannot delete message', err)
  }
}

async function createRoom(room) {
  try {
    return await httpService.post(`rooms/new`, room)
  } catch (err) {
    console.log('cannot create room', err)
  }
}

async function getRooms(filterBy = {}) {
  try {
    return httpService.get('rooms', { params: filterBy })
  } catch (err) {
    console.log('cannot get rooms', err)
  }
}


