// import { Avatar } from '@mui/material'
import React, { useEffect, useRef, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import ChatFooter from './ChatFooter'
import ChatMessage from './ChatMessage'
import { chatService } from '../services/chat-service'

export default function Chat({ messages, currentUser, rooms }) {
  const { id } = useParams()
  const [roomMessages, setRoomMessages] = useState([])
  const [currentRoom, setCurrentRoom] = useState('General Room')

  const bodyRef= useRef()
  const navigate = useNavigate()

  useEffect(() => {
    bodyRef.current.scrollTop = bodyRef.current.scrollHeight
    console.log(bodyRef.current.scrollHeight)
  })

  useEffect(() => {
    let roomMessagesData
    if (!currentUser) navigate('/')
    if (rooms.length) {
      let currRoom = rooms.filter(room => room._id === id)
      setCurrentRoom(currRoom[0].name)
    }
    if (messages) {
      roomMessagesData = messages.filter((message) => message.roomId === id)
      setRoomMessages(roomMessagesData)
    }
  }, [id, messages, currentUser, rooms, navigate])

  const deleteMessage = async (messageId) => {
    if (!messageId) return
    try {
        await chatService.deleteMessage(messageId)
    } catch(err) {
        console.log('CANT DELETE MSG', err)
    }

  }
  
  return (
    <div className="chat-container">
      <section className="chat-header">
        <div className="chat-info">
          <h3>{currentRoom}</h3>
        </div>
        <div className="chat-actions">
          {/* <IconButton>
            <SearchOutlined />
          </IconButton>
          <IconButton>
            <AttachFile />
          </IconButton>
          <IconButton>
            <MoreVert />
          </IconButton> */}
        </div>
      </section>
      <section ref={bodyRef} className="chat-body">
        {roomMessages.length? roomMessages.map((message) => (
          <ChatMessage key={message._id} message={message} currentUser={currentUser} deleteMessage={deleteMessage}/>
        )) : <p>No messages here yet..</p>}
      </section>
      <ChatFooter roomId={id} currentUser={currentUser}/>
    </div>
  )
}
