import React, { useEffect } from 'react'
import Avatar from '@mui/material/Avatar'
import MessageIcon from '@mui/icons-material/Message'
import IconButton from '@mui/material/IconButton'
import LogoutIcon from '@mui/icons-material/Logout'
import SearchInput from './SearchInput'
import SidebarChat from './SidebarChat'
import { useNavigate } from 'react-router-dom'
import { Tooltip } from '@mui/material'

export default function Sidebar({ rooms, currentUser, createRoom, logOut }) {
  const navigate = useNavigate()

  useEffect(() => {
    console.log('hi')
  }, [rooms])
  const selectRoom = (roomId) => {
    navigate(`/room/${roomId}`)
  }

  return (
    <div className="sidebar-container">
      <section className="sidebar-header">
        <div className="user-info">
          <Avatar src={currentUser?.imgUrl || ''} />
          <span className="user-name">{currentUser?.name}</span>
        </div>
        <div className="sidebar-actions">
          <Tooltip title="Create Room">
            <IconButton onClick={() => createRoom()}>
              <MessageIcon></MessageIcon>
            </IconButton>
          </Tooltip>
          <Tooltip title="Logout">
            <IconButton onClick={() => logOut()}>
              <LogoutIcon></LogoutIcon>
            </IconButton>
          </Tooltip>
        </div>
      </section>
      <section className="sidebar-search">
        <SearchInput />
      </section>
      <section className="sidebar-chats">
        {rooms.map((room) => (
          <SidebarChat key={room._id} room={room} selectRoom={selectRoom} />
        ))}
      </section>
    </div>
  )
}
