import React from 'react'
import { Avatar } from '@mui/material'
export default function SidebarChat({room, selectRoom}) {
  return (
    <div className='sidebar-chat-container' onClick={() => selectRoom(room._id)}>
        <Avatar src= {`https://robohash.org/set_set2/${room.name}`} />
      <section className='sidebar-chat-info'>
        <h2>{room.name}</h2>
      </section>
    </div>
  )
}
