import React from 'react'
import DeleteIcon from '@mui/icons-material/Delete'

export default function ChatMessage({ message, currentUser, deleteMessage }) {
  const messageDate = new Date(+message.timestamp).toString()
  return (
    <div className="chat-message-container">
      <p
        className={`chat-message ${
          message.name === currentUser.name.toLowerCase() ? 'sended-message' : ''
        }`}
      >
        <span className="chat-name">
          {message?.name}
          {message.name === currentUser.name.toLowerCase()? <button className="delete-icon" onClick={() => deleteMessage(message._id)}>
            <DeleteIcon />
          </button> : ''}
        </span>
        {message.message}
        <span className="chat-timestamp">{`${messageDate.slice(0, 24)}`}</span>
      </p>
    </div>
  )
}
