import { httpService } from './http-service'

export const userService = {
    query,
    saveUser
}

async function query() {
    try {
      return await httpService.get('users')
    } catch (err) {
      console.log('Cannot get users', err)
    }
  }


  async function saveUser(userInfo) {
    try {
        return await httpService.post('users/new', userInfo)
    }catch (err) {
        console.log('Cannot save user', err)
      }
  }