import { InsertEmoticon, MicOutlined } from '@mui/icons-material'
import { IconButton } from '@mui/material'
import React, { useRef, useState } from 'react'
import { chatService } from '../services/chat-service'
import data from '@emoji-mart/data'
import Picker from '@emoji-mart/react'

export default function ChatFooter({roomId, currentUser}) {
    const [input, setInput] = useState('')
    const inputRef = useRef()
    const containerRef = useRef()

    const addEmoji = (e) => {
      let sym = e.unified.split("-");
      let codesArray = [];
      sym.forEach((el) => codesArray.push("0x" + el));
      let emoji = String.fromCodePoint(...codesArray);
      setInput(input + emoji);
    }
  
    const sendMessage = async (ev) => {
        ev.preventDefault()
        if (!roomId || !currentUser.name || !input) return
        try {
            await chatService.saveMessage({name: currentUser.name.toLowerCase(), message: input, timestamp: Date.now(), roomId})
            setInput('')
        } catch(err) {
            console.log('CANT SAVE MSG', err)
        }
    }

  return (
    <div ref={containerRef} className="chat-footer hide-emoji">
      <div style={{position: 'relative'}}>
        <IconButton onClick={() => containerRef.current.classList.toggle('hide-emoji')}>
          <InsertEmoticon />
        </IconButton>
        <Picker data={data} onEmojiSelect={addEmoji}/>
      </div>
      <form>
        <input ref={inputRef} type="text" placeholder="Type a message" value={input} onChange={(e) => setInput(e.target.value)}/>
        <button type="submit" onClick={(ev) => sendMessage(ev)}>send</button>
      </form>
      <IconButton>
        <MicOutlined />
      </IconButton>
    </div>
  )
}
