import Sidebar from './cmps/Sidebar'
import Chat from './cmps/Chat'
import './assets/styles/global.scss'
import { useEffect, useRef, useState } from 'react'
import Pusher from 'pusher-js'
import { chatService } from './services/chat-service'
import { userService } from './services/user-service'
import { utilService } from './services/util-service'
import { HashRouter as Router, Route, Routes, Navigate } from 'react-router-dom'
import SignIn from './cmps/SignIn'
import AppHeader from './cmps/AppHeader'
import AppFooter from './cmps/AppFooter'

function App() {
  const [messages, setMessages] = useState([])
  const [users, setUsers] = useState([])
  const [currentUser, setCurrentUser] = useState(null)
  const [rooms, setRooms] = useState([])
  const appBodyRef = useRef()

  useEffect(() => {
    async function fetchData() {
      const messagesData = await chatService.query()
      setMessages(messagesData)
      const usersData = await userService.query()
      setUsers(usersData)
      const roomsData = await chatService.getRooms()
      setRooms(roomsData)
    }
    fetchData()
    const user = utilService.loadFromStorage('chatUser')
    if(user) setCurrentUser(user)
  }, [])

  useEffect(() => {
    const pusher = new Pusher('c3cf189ab5e7ff083222', {
      cluster: 'eu',
    })

    var channel = pusher.subscribe('messages')
    channel.bind('inserted', (newMessage) => {
      setMessages([...messages, newMessage])
    })
    channel.bind('deleted', async () => {
      const data = await chatService.query()
      setMessages(data)
    })
    return () => {
      channel.unbind_all()
      channel.unsubscribe()
    }
  }, [messages])

  const signIn = async (ev, userInfo) => {
    ev.preventDefault()
    const isExist = users.some(user => user.name.toLowerCase() === userInfo.name.toLowerCase())
    if (isExist) {
      setCurrentUser(userInfo)
      utilService.saveToStorage('chatUser', userInfo)
    }
    else {
      const data = await userService.saveUser(userInfo)
      if (data) {
        setCurrentUser(userInfo)
        utilService.saveToStorage('chatUser', userInfo)
      }
    }
  }

  const logOut = () => {
    localStorage.removeItem('chatUser')
    window.location.reload()
  }
  
  const createRoom = async () => {
    const roomName = prompt('Enter room name')
    if (!roomName) return
    const isRoomExist = rooms.filter(room => room.name.toLowerCase() === roomName.toLowerCase())
    if (isRoomExist[0]) {
      alert('Room already exist! try another name')
    }
    else {
      const newRoom = await chatService.createRoom({name: roomName})
      setRooms([...rooms, newRoom])
    }
  }

  return (
    <Router>
      <div className="app">
        <div ref={appBodyRef} className="app-body">
          <AppHeader />
          <div className='main-app'>
            <Routes>
              <Route exact path="/room/:id" element={[<Sidebar key='sidebar' rooms={rooms} currentUser={currentUser} createRoom={createRoom} logOut={logOut}/>, <Chat messages={messages} key='chat' currentUser={currentUser} rooms={rooms}/>]} />
              <Route exact path="/" element={currentUser ? <Navigate to="/room/633bcc076348598b21f01c23"/> : <SignIn signIn={signIn}/>}/>
            </Routes>
          </div>
        </div>
      </div>
      <AppFooter />
    </Router>
  )
}

export default App
